.app__skills{
    flex:1;
    width:100%;
    flex-direction: column;
    
}

.app__skills-container{
    width: 80%;
    margin : 3rem auto 0 auto;
    display: flex;
    flex-direction: row;

    @media screen and (max-width:1024px) {
        width:100%;
        flex-direction: column;
    }   
}

.app__skills-list{
    flex:1;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    
    margin:auto;

    @media screen and (max-width:1024px) {
        margin-right: 0;
        justify-content: center;
        align-items: center;
    }
}

.app__skills-item{
    display: flex;
    flex-direction: column;
    text-align: center;
    margin:1rem;
    transition: all 0.3s ease-in-out;

    div{
        width: 90px;
        height: 90px;
        background-color: #fff;
        border-radius: 25%;
        // clip-path: polygon(21% 0, 85% 0, 100% 0, 100% 83%, 83% 100%, 30% 100%, 0 100%, 0 17%);
        img{
            width:50%;
            height: 50%;
            transition: all .5s ease-in-out;
        }
        
        &:hover{
            // box-shadow: 0 0 25px #fef4f5;
            box-shadow: 2px 0 15px rgba(255, 255, 255, 0.8);

            img{
                width: 65%;
                height:65%
                
            }

        }
        @media screen and (min-width:2000px) {
            width:150px;
            height:150px;
        }
        
        @media screen and (max-width:1024px) {
            width:70px;
            height:70px;
        }
    }

    p{
        font-weight: 500;
        margin: top 0.5rem;

    }
    
    @media screen and (min-width:2000px) {
        margin : 1rem 2rem;

        p{
            margin-top: 1rem;
        }
    }
}